import { makeStyles, useTheme } from '@material-ui/styles';
const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: '2rem',
  },
  connectWalletBtnGroup: {
    [theme.breakpoints.down('xs')]: {
      display: 'flex',
      flexDirection: 'column-reverse',

      '& $button': {
        width: '100% !important',
        marginBottom: "1.8rem"
      }
    }
  },
  totalBalance: {

  },
  button: {
    color: '#8087ED',
    border: '1.5px solid #8087ED',
    textTransform: 'none',
    padding: theme.spacing(0.5, 3)
  },
  balance_desc: {
    color: theme.palette.text_sub,
    fontSize: 16,
    marginRight: theme.spacing(1),
    display: 'flex',
    alignItems: 'center'
  },
  amount: {
    color: theme.palette.text_main,
    fontSize: 20,
    display: 'flex',
    alignItems: 'center'
  },
  table: {
    padding: theme.spacing(3),
    borderRadius: 24,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: 'none',
    
    '& tbody':{
      '& tr:last-child': {
        '& td':{
          borderBottom: "none"
        }
      },
    },

    [theme.breakpoints.down('xs')]: {
      padding: 0,
      '& thead': {
        padding: ".4rem",
        '& .MuiTableRow-root': {
          backgroundColor: theme.palette.wallet_button_color,
        }
      },

      '& tbody':{
        '& tr': {
          height: "40px",
        },
        '& tr:last-child': {
          '& td':{
            borderBottom: "none"
          }
        },
        
        '& .MuiTableCell-root': {
          padding: "1rem .4rem",
        }
        
      },

    },

  }
}));

export default useStyles;
