import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Box, Button, Card, Icon } from '@material-ui/core';
import useStyles from './style';
import { useSelector } from 'react-redux';
import { ConnectWalletModal, WalletTableCard } from 'components';
import { getCorrectDecValue } from 'web3/contracts';

const Wallet = props => {
  const theme = useTheme();
  const classes = useStyles(theme);

  const [sort_column, setSortColumn] = useState(0);
  const [sort_method, setSortMethod] = useState("ASC");
  const [walletModalOpen, setWalletModalOpen] = useState(false);

  const walletList = useSelector(state => state.app.walletList);
  const [totalBalance, setTotalBalance]= useState(0);
  const columns = [];
  const cellList = [
    { key: 'name', field: 'Name' },
    { key: 'balance', field: 'Balance', icon: "sort" },
    { key: 'control', field: '' },
  ];
  const handleSort = (index, direct) => {
    setSortColumn(index);
    setSortMethod(direct);
  }

  useEffect(() => {
    let res = 0;
    for (let i = 0; i < walletList.length; i ++) {
      res += Number(walletList[i].balance || 0);
    }
    setTotalBalance(res);
  }, [walletList])


  return (
    <Box className={classes.root}>
      <Box style={{display: 'flex', justifyContent: 'space-between', marginBottom: '1rem'}}   className={classes.connectWalletBtnGroup}>
        <Box style={{display: 'flex'}} className={classes.totalBalance}>
          <Box className={classes.balance_desc}>
            Total balances:
          </Box>
          <Box className={classes.amount}>
            {`$ ${getCorrectDecValue(totalBalance, 0, -1)}`}
          </Box>
        </Box>
        <Button
          variant="outlined"
          className={classes.button}
          onClick={() => setWalletModalOpen(true)}
        >
          + Connect Wallet
        </Button>
      </Box>
      <Card className={classes.table}>
        <WalletTableCard
          onSelectSort={handleSort}
          columns={columns}
          products={walletList}
          cells={cellList}
        />
      </Card>
      <ConnectWalletModal 
        open={walletModalOpen}
        handleClose={() => setWalletModalOpen(false)}
      />
    </Box>
  );
};

export default Wallet;
